module.exports = "<app-header></app-header>\n\n   <!-- PAGE TITLE SECTION-->\n    <section class=\"pageTitleSection\">\n      <div class=\"container\">\n        <div class=\"pageTitleInfo\">\n          <h2>About us</h2>\n          <ol class=\"breadcrumb\">\n            <li><a href=\"index.html\">Home</a></li>\n            <li class=\"active\">About us</li>\n          </ol>\n        </div>\n      </div>\n    </section>\n <section class=\"product-review clearfix\">\n      <div class=\"container\">\n        \n        <div class=\"tabCommon\">\n          \n\n          <div class=\"tab-content\">\n            <div id=\"menu1\" class=\"tab-pane fade in active\">\n              <div class=\"media\">\n                <h3>About Our Pre-School</h3>\n                <p>Barrowby Preschool is a Parent Committee run preschool, providing high quality care and learning experiences for children aged two years to school age, in Barrowby, Grantham and the surrounding area.</p>\n\n<p>We aim to give your child the opportunity to learn through play in a safe, caring and stimulating environment, whilst enjoying the company of other children and adults.\nWe appreciate that all children are individuals and aim to provide learning experiences catered to your child’s individual needs. This enables every child to develop at their own pace and grow in self-confidence by choosing from a wide range of activities. At the end of their time with us, children will be independent and able to move on happily to school.</p>\n\n<p>When your child starts at Barrowby Preschool you will be allocated a Keyworker. Your child’s keyworker will work closely with your child, forming a special bond and helping them to settle in well. Your keyworkers role is to observe your child develop, plan engaging activities based on their needs and interests and assess how their learning is progressing.</p>\n\n<p>To do this we follow The Early Years Foundation Stage (EYFS). The EYFS is split into seven areas of learning, divided into prime and specific areas. The three prime areas are: Personal, Social and Emotional Development, Communication & Language and Physical Development. Prime areas are fundamental, work together and are needed to support learning in all other areas. The specific areas include: Literacy, Mathematics, Understanding the World and Expressive Arts and Design. These areas include essential skills and knowledge.</p>\n\n<p>We are open during term time and follow the same terms as Barrowby Primary school.\nOur sessions run 9-12 every day. We also run lunch club 12-1 and currently this is on Tuesday and Thursday. Each session costs £11 and lunch club is £3.50.</p>\n\n<p>The term after your child’s 3rd birthday, they are eligible for funding for 15 hours each week.</p>\n\n<p>Barrowby Preschool has 4 permanent members of staff, and 4 relief staff. We can have up to 28 children in our care.</p>\n\n<p>Jessica Armstrong – Preschool Manager – BA (Hons) Primary Education\nMarj Spendlow – Deputy Manager – NVQ Level 4 – Childcare & Education\nRobbyn (Bobs) Smith – Key Worker – BA Early Childhood Studies\nJenny Wright – Keyworker (Fridays) – NVQ Level 3 – Childcare & Development\nRelief Staff: Mary-Jane, Sue, Alice & Jenny</p>\n\n<p>What to bring\nPlease ensure your child has:-\nA spare change of clothes with them in case of little accidents.\nSlippers or indoor shoes\nSuitable outside clothing e.g. Sun hats, welly boots, woolly hat etc.</p>\n\n<p>If your child wants to bring their comforter they can bring it in but will be encouraged to leave it in their tray where it is safe.</p>\n\n<p>A session at Preschool</p>\n\n<p>When you arrive help your child find their tag on the board, place it on a hook with all their belongings. Help them get their slippers on and you’re ready to go!</p>\n\n<p>9am – Doors open.\nMake your way into Preschool. Your child can find their name label and stick it on a tray. You may find some letters or artwork in here later. The children can find their friends and choose where they would like to play. If you have any messages for the Preschool staff please inform the staff member at the door, or Jess the manager.</p>\n\n<p>9.15 am – Welcome time.\nThe children greet each other with our Hello song. We look at the calendar, count all the children and talk about what we will be doing today.\n</p>\n<p>\n9.30 am – Snack cafe / focussed activities / free flow\nThe children choose which activities they would like to take part in. There is always lots to choose from, such as messy painting, sand, water, quieter table top activities like jigsaws, the role play area or small world and construction area. Our staff play alongside the children and work to observe, assess and develop their learning. They also lead focussed activities during this time which enable their key children to learn and achieve their next steps.\nThe snack cafe is open throughout the morning so children can choose when they would like a break for snack. Usually we have fruit and a biscuit to eat with either milk or water. The children are encouraged to pour their own drink and to tidy up after themselves.\n</p>\n<p>\n11 am – Tidy up time\n</p>\n<p>\n\n11.15 am – Garden time\nWe have a lovely garden which we use every day, whatever the weather. There is usually lots of games to take part in, like ‘What’s the time Mr Wolf’, ball games and circle games.\n</p>\n<p>\n11.45 am – Circle time\nWe play all sorts of games at circle time, that involve letters and sounds, counting, colours etc. this is usually followed by a story and singing the Goodbye song.\n</p>\n                <ul class=\"list-unstyled para-list\">\n                  <li><i class=\"fa fa-check\" aria-hidden=\"true\"></i>Key Workers</li>\n                  <li><i class=\"fa fa-check\" aria-hidden=\"true\"></i>Early Years Foundation Stage</li>\n                  \n                </ul>\n                <ul class=\"list-unstyled para-list\">\n                  <li><i class=\"fa fa-check\" aria-hidden=\"true\"></i>Sessions 9-12 Every Day</li>\n                  <li><i class=\"fa fa-check\" aria-hidden=\"true\"></i>Places For Up To 28 Children</li>\n                  \n                </ul>\n              </div>\n            </div>\n            \n          </div>\n        </div>\n          \n      </div>\n    </section>\n\n\n\n<app-footer></app-footer>"

